.detailed-skills {
  margin-top: 200px;
}

.detailed-skills__inner {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 20px;
  row-gap: 150px;
  padding: 20px;
}

.detailed-skills__area {
  position: relative;
  z-index: 0;
  border: 2px solid $main-orange;
  padding-top: 150px;
  background-color: $bg-semi-light;

  &:hover {
    border-color: $main-orange-hover;

    .detailed-skills__heading {
      background-color: $main-orange-hover;
    }

    .detailed-skills__heading-text {
      color: $main-dark;
    }
  }
}

.detailed-skills__heading {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 6;
  top: -76px;
  left: 126px;
  margin: 0;
  border: 2px solid $main-orange;
  width: 144px;
  height: 144px;
  background-color: $main-dark;
  transform: rotate(45deg);
  transition: 0.4s;
}

.detailed-skills__heading-text {
  position: relative;
  transform: rotate(-45deg);
  transition: 0.4s;
  text-align: center;
}

.detailed-skills__subheading {
  text-align: center;
}

.detailed-skills__item {
  margin-bottom: 10px;
  font-size: 18px;
}
