.sort {
  // FIXME: Нормально отпозиционировать
  width: 700px;
  margin: 0 auto;
}


.sort__item {
  position: relative;
  margin-right: 40px;

  &:hover {
    cursor: url("../../img/icons/cursor-pointer.png"),
    pointer;
  }

  &:active {
    transform: scale(0.95);
  }
}

.sort__label {
  padding: 10px 20px;
  padding-right: 60px;
  font-size: 27px;

  @include pseudo(before) {
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    width: 37px;
    height: 37px;
    border-radius: 50%;
    border: 3px solid $main-orange;
  }

  @include pseudo(after) {
    right: 9px;
    top: 50%;
    transform: translateY(-50%);
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: $main-orange;
    opacity: 0;
    transition: opacity 0.03s ease-in;
  }

  &:hover {
    cursor: url("../../img/icons/cursor-pointer.png"),
    pointer;
  }
}

.sort__input {
  width: 1px;
  height: 1px;
  margin: -1px;
  opacity: 0;
  font-size: 0;

  &:checked + .sort__label::after {
    opacity: 1;
  }

  &:focus + .sort__label {
    outline: 1px solid $main-dark;
    box-shadow: 0 2px 10px $main-orange;
    border-bottom: 2px solid $main-orange;
  }
}
