.header {
  padding-bottom: 78px;
  background-color: $main-dark;
}

.header__inner {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  padding-top: 30px;
  width: 1440px;
}

.header__nav {
  margin-right: -10px;
  list-style-type: none;
  box-shadow: 0 10px 20px $main-orange-hover;
  border-radius: 20px;
}

.header__nav-link {
  border-top: 3px solid $main-dark;
  font-size: 18px;
  text-transform: uppercase;
  border-radius: 20px;

  &--curent {
    border-color: $main-orange-hover;
  }

  a {
    color: $light;
    display: inline-block;
    padding: 20px 10px;
    text-decoration: none;
  }

  a:hover {
    cursor: url("../../img/icons/cursor-pointer.png"),
    pointer;
  }
}

