.filter {
  // FIXME: Нормально отпозиционировать
  width: 800px;
  margin: 0 auto;
  margin-bottom: 50px;
}

.filter__item {
  position: relative;
  margin-right: 40px;
  transition: transform 0.03s ease-in;

  &:active {
    transform: scale(0.95);
  }
}

.filter__label {
  padding: 10px 20px;
  padding-right: 60px;
  font-size: 27px;
  // FIXME:

  @include pseudo(before) {
    top: 50%;
    transform: translateY(-50%);
    right: 5px;
    width: 38px;
    height: 38px;
    border: 3px solid $main-orange;
    border-radius: 3px;
    // FIXME:
  }

  @include pseudo(after) {
    right: 13px;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    background-image: url("../../img/icons/icon-checkbox.svg");
    background-size: cover;
    opacity: 0;
    transition: opacity 0.03s ease-in;
  }

  &:hover {
    cursor: url("../../img/icons/cursor-pointer.png"), pointer;
  }
}

.filter__input {
  width: 1px;
  height: 1px;
  margin: -1px;
  opacity: 0;
  font-size: 0;

  &:checked + .filter__label::after {
    opacity: 1;
  }

  &:focus + .filter__label {
    outline: 1px solid $main-dark;
    box-shadow: 0 2px 10px $main-orange;
    border-bottom: 2px solid $main-orange;
  }
}
