.card {
  // background-image: url("../img/cat.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  display: block;
  position: relative;
  transition: all 0.05s, box-shadow 0.3s;
  box-shadow: 0 0 15px rgba($main-orange-hover, 0.6);
  border-radius: 15px;
  border: 3px solid $main-dark;

  @include pseudo(before) {
    z-index: 1;
    border-radius: 15px;
    opacity: 0.01;
    width: 100%;
    height: 100%;
    background-color: rgba($main-dark, 0.5);
  }

  @include pseudo(after) {
    right: 10px;
    top: 10px;
    opacity: 0.01;
    z-index: 2;
    width: 30px;
    height: 30px;
    background-image: url("../img/icons/icon-link.svg");
    background-size: cover;
    background-position: center;
  }

  &:hover {
    box-shadow: 0 0 40px $main-orange-hover;
    cursor: url("../../img/icons/cursor-pointer.png"),
    pointer;

    &::before {
      opacity: 1;
    }

    &::after {
      opacity: 1;
    }

    .card__overlay {
      display: flex;
      opacity: 1;
    }
  }
}

.card__overlay {
  z-index: 2;
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
  left: 0;
  padding: 1% 5%;
  position: absolute;
  top: 0;
  width: 100%;
  opacity: 0.01;
  transition: 0.3s opacity ease;
  text-decoration: none;

  &:hover {
    cursor: url("../../img/icons/cursor-pointer.png"),
    pointer;
  }
}

.card__img {
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 15px;
}

.card__title {
  text-shadow: 2px 2px $main-dark;
  padding: 10px 25px;
  border-radius: 10px;
  color: $main-orange;
  background-color: rgba($main-dark, 0.4);
  font-size: 30px;
}

.card__descr {
  padding: 15px 25px;
  border-radius: 10px;
  background-color: rgba($main-dark, 0.4);
  text-align: center;
  font-size: 18px;
  color: $light;
  text-shadow: 2px 2px $main-dark;
}
