.skillset {
  position: relative;
  z-index: 10;
  margin-bottom: 250px;
  color: white;
}

.skillset__card {
  display: grid;
  grid-template-columns: 416px 856px;
  gap: 72px;
}

.skillset__main-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.skillset__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.skillset__main-heading {
  font-weight: 700;
  font-size: 55px;
  line-height: 66px;
}

.skillset__button-more {
  align-self: center;
  display: inline-block;
  border: 2px solid $main-orange;
  padding: 20px 32px;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: $light;
  background-color: transparent;
}
