.portfolio {
  padding: 0 120px;
}

.portfolio__list {
  display: flex;
  list-style: none;
  margin: 0;
  flex-wrap: wrap;
  padding: 0;
  justify-content: center;
}

.portfolio__item {
  align-items: start;
  height: 300px;
  margin: 60px 50px;
  width: 20%;
}

.portfolio__heading {
  margin-bottom: 50px;
  text-align: center;
  font-weight: 700;
  font-size: 60px;
}

.portfolio__menu-item {
  display: flex;
}
